import apiCore from '@/utils/apiCore'

const HTTP = new apiCore({
  baseURL: process.env.VUE_APP_URL,
  headers: {
    'Content-Type': 'application/json;charset=utf-8',
  },
})

export function getContractPages(params: any) {
  return HTTP.get('/buss/contract/page', params)
}
/**
 * @Author roct
 * @Description 获取合同详情
 * @Date 22:14 2021/5/20 0020
 **/
export function getContractInfo(params: any) {
  return HTTP.get('/buss/contract/getContractInfo', params)
}

/**
 * @Author roct
 * @Description 获取合同模板列表
 * @Date 22:14 2021/5/20 0020
 **/
export function getContractTemplateList(params: any) {
  return HTTP.get('/buss/contract/template/page', params)
}

/**
 * @Author roct
 * @Description 删除合同模板列表
 * @Date 22:14 2021/5/20 0020
 **/
export function deleteContractTemplate(id: any) {
  return HTTP.delete('/buss/contract/template/' + id)
}

/**
 * @Author roct
 * @Description 获取设备列表
 * @Date 22:14 2021/5/20 0020
 **/
export function getDeviceList(params: any) {
  return HTTP.get('/buss/device/page', params)
}
/**
 * @Author roct
 * @Description 修改设备信息
 * @Date 15:07 2021/5/22 0022
 **/
export function updateDeviceInfo(params: {
  deviceCode: any
  deviceName: any
  deviceType: any
  id: any
  projectId: any
  roomId: any
}) {
  return HTTP.put('/buss/device', params)
}

/**
 * @Author roct
 * @Description 新增设备信息
 * @Date 15:07 2021/5/22 0022
 **/
export function addDevice(params: {
  count: any
  deviceCode: any
  deviceName: any
  deviceType: any
  id: any
  projectId: any
  roomId: any
}) {
  return HTTP.post('/buss/device', params)
}
/**
 * @Author roct
 * @Description 获取账单信息
 * @Date 16:33 2021/5/22 0022
 **/
export function getBillList(params: { contractId: number | string, current: number, size: number }) {
  return HTTP.get('/buss/contract/getBill', params)
}
