 * name: contractDetail
 * description:
 * author: roct
 * date: 22:27 2021/5/19 0019
 */
import { Component, Vue } from 'vue-property-decorator'
import { getContractInfo, getBillList } from '@/api/contract/contractApi'
@Component({
  components: {}
})
export default class contractDetail extends Vue {
  private pageTitle = '合同详情'
  // 合同id
  private contractId = ''
  // 合同信息
  private contractData: any = ''
  // 账单信息
  private billList: any[] = []
  // 当前选中的页面
  private activeName = '0'
  public mounted(): void {
    this.contractId = this.$route.query.id + ''
    if (this.contractId === 'undefined') {
      this.contractId = Vue.ls.get('/contract/detail') + ''
    }
    this.getContractInfoRequest()
    this.queryBillList()
  }
  // 切换页面
  private handleClick() {}

  // 分页数据
  private pagination = {
    total: 0,
    current: 1,
    size: 10
  }
  private goBack() {
    this.$router.back()
  }
  /**
   * @Author roct
   * @Description 修改当前页
   * @Date 20:08 2021/5/19 0019
   **/
  private handleCurrentChange(e: any) {
    this.pagination.current = e
    this.queryBillList()
  }
  /**
   * @Author roct
   * @Description 获取合同详情
   * @Date 22:14 2021/5/20 0020
   **/
  private async getContractInfoRequest() {
    const res = await getContractInfo({ contractId: this.contractId })
    if (res.code === 200) {
      console.log('res', res)
      this.contractData = res.data
    }
  }
  /**
   * @Author roct
   * @Description 获取账单信息
   * @Date 16:35 2021/5/22 0022
   **/
  private async queryBillList() {
    const query = Object.assign(
      { contractId: this.contractId },
      this.pagination
    )
    const res = await getBillList(query)
    if (res.code === 200) {
      this.billList = res.data.records
      this.pagination.total = res.data.total
    }
  }
}
